.modal {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;
}

.modal.active {
  opacity: 1;
  pointer-events: all;
}

.modal-container {
  /* padding: 0 40px 40px 40px; */
  border-radius: 12px;
  background-color: white;
  width: 70%;
  margin: 0 auto;
  display: flex;
}

.slider-img {
  max-width: 450px;
  background-repeat: no-repeat;
  border-radius: 12px 0px 0 12px;
  background-size: contain;
  background-position: center;
  height: 600px;
}

.button {
  display: none;
  position: absolute;
}

.slider-container {
  max-width: 450px;
}

.slider-container:hover .button {
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;

  border-radius: 100%;

  background-color: white;
}

.button:hover {
  cursor: pointer;
  opacity: 0.7;
}

.modal-description-container {
  padding: 20px 40px;
}

.modal-title {
  font-size: 1.4em;
  font-weight: 700;
  font-family: "Cormorant Garamond", serif;
}

.modal-price {
  font-size: 1.4em;
  font-family: "Cormorant Garamond", serif;
  font-weight: 700;
  padding: 20px 0;
}

.modal-description {
  font-family: "EB Garamond", serif;
  font-size: 1.4em;
  /* font-weight: 700; */
  text-align: justify;
  color: var(--price-color);
}
.modal-button-order {
  margin: 0 auto;
  margin-top: 50px;
  width: 250px;
  height: 50px;
  background-color: var(--price-color);
  color: white;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  border-radius: 25px;
  display: flex;
  justify-content: center;
  border: 1px solid var(--price-color);
  align-items: center;
}

.modal-button-order:hover {
  cursor: pointer;
  color: black;
  background-color: white;
}
.close-modal-icon {
  width: 24px;
  height: 24px;
}

.modal-content-container {
  display: flex;
  flex-direction: column;
}

.close-modal {
  display: flex;
  position: relative;
  padding-top: 25px;
  left: 92.5%;
}

.close-modal:hover {
  cursor: pointer;
  opacity: 0.7;
}
@media only screen and (max-width: 1200px) {
  .modal-container {
    width: 80%;
  }
  .modal-description-container {
    padding: 10px 30px;
  }
}

@media only screen and (max-width: 992px) {
  .modal-container {
    width: 80%;
  }
  .modal-description-container {
    padding: 8px;
  }
  .close-modal {
    left: 86%;
    padding-top: 20px;
  }
  .modal-description {
    font-size: 1em;
  }
  .slider-img {
    max-width: 350px;
    height: 466px;
  }
}
@media only screen and (max-width: 768px) {
  .close-modal {
    padding-top: 10px;
  }
  .modal-title {
    font-size: 1.2em;
  }
  .modal-price {
    padding: 10px 0;
    font-size: 1.2em;
  }
  .modal-description {
    font-size: 0.9em;
  }
  .modal-button-order {
    margin-top: 10px;
  }
}
@media only screen and (max-width: 480px) {
  .slider-container {
    width: 350px;
  }
  .slider-img {
    border-radius: 12px 12px 0 0;
  }
  .modal-container {
    width: 350px;
    flex-direction: column;
  }
  .modal-content-container {
    max-width: 350px;
  }
  .close-modal {
    width: 25px;
    top: -450px;
    z-index: 3;
  }
}
