header {
  background-color: var(--header-color);
  padding: 20px 0px;
  border: 2px 2px 0px 0px solid var(--price-color);
  border-radius: 20px 20px 0 0;
}
.header-wrapper {
  width: 80%;
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  justify-content: space-between;
}
.logo {
  background-image: url("./logo.png");
  background-size: contain;
  width: 15em;
  height: 70px;
  background-repeat: no-repeat;
}
.logo:hover {
  opacity: 0.7;
}
nav {
  padding-top: 30px;
  margin: 0 0 0 -60px;
  width: 60%;
  display: flex;
  justify-content: space-between;
}
.nav-item {
  color: var(--price-color);
  font-weight: 600;
  font-family: "Outfit", sans-serif;
}
.nav-item:hover {
  opacity: 0.7;
}
li {
  list-style: none;
  cursor: pointer;
}

.number {
  padding-top: 25px;
  display: flex;
}
.number-icon {
  width: 24px;
  height: 24px;
}
@media only screen and (max-width: 992px) {
  .header-wrapper {
    width: 90%;
  }
  .logo {
    margin-top: 5px;
    width: 160px;
    height: 50px;
  }
}
@media only screen and (max-width: 768px) {
  nav {
    width: 50%;
  }
  .nav-item {
    font-size: 12px;
  }
  .number {
    font-size: 12px;
  }
  .logo {
    margin-top: 5px;
    width: 120px;
    height: 50px;
  }
}
